html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
  position: relative;
  overflow-x: hidden;
  background: linear-gradient(to bottom, transparent, transparent 80%, #5ccfd3 80%, #5ccfd3 100%);
}
body p {
  max-width: 500px;
  margin: 0 2.5rem;
  font-size: 14px;
  font-weight: 500;
}
@media (min-width: 40em) {
  body p {
    font-size: 16px;
  }
}
@media (min-width: 71.875em) {
  body p {
    margin: 0 5rem;
    font-size: 28px;
  }
}
body h1 {
  max-width: 500px;
  margin: 0 2.5rem;
  font-weight: 700;
  font-size: 2rem;
  color: #5ccfd3;
}
@media (min-width: 40em) {
  body h1 {
    font-size: 40px;
  }
}
@media (min-width: 71.875em) {
  body h1 {
    margin: 0 5rem;
    font-size: 60px;
  }
}
body a {
  text-decoration: none;
}

.hidden {
  display: none !important;
}

.show {
  display: block !important;
}

.btn {
  display: block;
  cursor: pointer;
}

.btn-nav {
  background-color: transparent;
  border: none;
  transition: 150ms all ease-in-out !important;
}
.btn-nav:hover svg {
  width: 100%;
  height: 100%;
}
.btn-nav:hover svg g line,
.btn-nav:hover svg g path {
  stroke: #5ccfd3;
  fill: #5ccfd3;
}

.btn-carousel {
  width: 48px;
  height: 96px;
  position: absolute;
  top: 50%;
  background-color: #ffffff;
  color: #5ccfd3;
  font-size: 12px;
  font-weight: 700;
  border: none;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}
@media (min-width: 40em) {
  .btn-carousel {
    width: 48px;
    height: 48px;
    border-radius: 0.5rem;
  }
}
.btn-carousel.left {
  left: 0;
  border-radius: 0 0.5rem 0.5rem 0;
}
@media (min-width: 40em) {
  .btn-carousel.left {
    top: 0;
  }
}
.btn-carousel.right {
  right: 0;
  border-radius: 0.5rem 0 0 0.5rem;
}
@media (min-width: 40em) {
  .btn-carousel.right {
    background-color: #5ccfd3;
    top: 40px;
    right: 3rem;
    border-radius: 0.5rem !important;
    box-shadow: 0px 0px 4px 0px #5ccfd3;
    color: #ffffff;
  }
}
@media (min-width: 40em) {
  .btn-carousel.right {
    right: 5rem;
  }
}

.btn-share {
  width: 296px;
  height: 48px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: center;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 14px;
  color: #ffffff;
  border: none;
  border-radius: 0.5rem;
  transition: 150ms all ease-in-out !important;
}
.btn-share:hover {
  transform: scale(0.95);
}
.btn-share.twitter {
  background-color: #0da1f2;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}
.btn-share.facebook {
  background-color: #4267b2;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

nav {
  width: 100%;
  height: 100px;
  padding: 2rem;
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: white;
}
@media (min-width: 40em) {
  nav {
    height: 125px;
  }
}
@media (min-width: 71.875em) {
  nav {
    height: 180px;
    padding: 3rem 5rem;
  }
}
nav .nav-left img {
  width: 100px;
  height: auto;
}
nav .nav-left img:hover {
  color: #5ccfd3;
}
@media (min-width: 40em) {
  nav .nav-left img {
    height: 125px;
  }
}
@media (min-width: 71.875em) {
  nav .nav-left img {
    width: 180px;
  }
}
nav .nav-right .btn-navs-wrapper {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: center;
  align-self: center;
}

.menu-container, .share-container {
  position: absolute;
  top: 100px;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
  z-index: 100;
}
@media (min-width: 40em) {
  .menu-container, .share-container {
    top: 125px;
  }
}
@media (min-width: 71.875em) {
  .menu-container, .share-container {
    top: 150px;
  }
}
.menu-container .menu-list, .share-container .menu-list, .share-container .share-list {
  width: 100%;
  padding: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -85%);
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  list-style: none;
}
.menu-container .menu-list .menu-item, .share-container .menu-list .menu-item, .share-container .share-list .menu-item {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-container .menu-list .menu-item .menu-link, .share-container .menu-list .menu-item .menu-link, .share-container .share-list .menu-item .menu-link {
  width: 100%;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  text-align: center;
  color: #000000;
  font-weight: 500;
  cursor: pointer;
  transition: 150ms all ease-in-out !important;
}
@media (min-width: 40em) {
  .menu-container .menu-list .menu-item .menu-link, .share-container .menu-list .menu-item .menu-link, .share-container .share-list .menu-item .menu-link {
    font-size: 20px;
  }
}
@media (min-width: 71.875em) {
  .menu-container .menu-list .menu-item .menu-link, .share-container .menu-list .menu-item .menu-link, .share-container .share-list .menu-item .menu-link {
    font-size: 28px;
  }
}
.menu-container .menu-list .menu-item .menu-link:hover, .share-container .menu-list .menu-item .menu-link:hover, .share-container .share-list .menu-item .menu-link:hover {
  color: #5ccfd3;
}

.carousel {
  position: relative;
}
.carousel .carousel-inner {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: content-box;
  overflow-y: visible;
  overflow-x: scroll;
  scroll-behavior: smooth;
  scrollbar-width: none;
}
@media (min-width: 40em) {
  .carousel .carousel-inner {
    padding-bottom: 3rem;
  }
}
@media (min-width: 71.875em) {
  .carousel .carousel-inner {
    margin-left: 2rem;
    margin-top: 3rem;
  }
}
.carousel .carousel-inner::-webkit-scrollbar {
  width: 0 !important;
}
.carousel .carousel-inner .carousel-item {
  min-width: 300px;
  width: 80%;
  max-width: 312px;
  min-height: 400px;
  height: 65vh;
  max-height: 510px;
  margin-left: 1.5rem;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .carousel .carousel-inner .carousel-item {
    margin-left: 2rem;
  }
}
.carousel .carousel-inner .carousel-item:last-of-type {
  margin-right: 1.5rem;
}
@media (min-width: 71.875em) {
  .carousel .carousel-inner .carousel-item:last-of-type {
    margin-right: 2rem;
  }
}
.carousel .carousel-inner .carousel-item .product-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
}
.carousel .carousel-inner .carousel-item .product-wrapper .slider-card-img {
  display: block;
  overflow: hidden;
}
.carousel .carousel-inner .carousel-item .product-wrapper .slider-card-img img {
  width: auto;
  height: 100%;
}
.carousel .carousel-inner .carousel-item .product-wrapper .slider-card-info {
  width: 100%;
  height: 20%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
}
.carousel .carousel-inner .carousel-item .product-wrapper .slider-card-info h2 {
  font-size: 24px;
  font-weight: 500;
  margin: 1.25rem 2rem;
  color: #ffffff;
}